import React, { CSSProperties } from "react";
import '../ProofCard/ProofCard.css'

type cardProps = {
    title: string,
    dataList: any[],
    style?:CSSProperties,
}

export default function ProofCard(props: cardProps): JSX.Element {
    return (
        <div style={{paddingBottom:'16px'}}>
            <div className='detail-header'>
                <div>
                    <span className="detail-title">{props.title}</span>
                </div>
                <div style={{paddingBlockStart:'20px'}}>
                    {
                        props.dataList.map((item:any) => 
                            <div key={item.title} className='detail-header-info'>
                                <div className='detail-header-info-title'>{item.icon} <span style={{marginLeft:'8px'}}>{item.title}</span></div>
                                <div className='detail-header-info-value'>{<span><span>{item.value}</span>{item.suffix?<span className="detail-header-info-value-suffix">{item.suffix}</span>:''}</span>}</div>
                            </div>
                        )
                    }
                </div>
            </div>
        </div>
    )
}